  box-shadow: 0.5rem 0.5rem 1rem 0 #070305;
  border-radius: 0.4rem;
  height: 10vh;
  border: 0.5px solid #e51c1e;
  transition: all 0.4s; }
  .card:hover {
    transform: translateY(4px); }
  .card:hover .card__side-back {
    transform: translateY(0); }
  .card:hover .card__side-front {
    transform: translateY(-100%); }
  .card__text {
    padding: 1.2rem; }
    .card__text span {
      font-family: "roboto", sans-serif; }
  .card__image {
    max-width: 3rem;
    height: 3rem; }
    .card__image-cont {
      height: 100%;
      background-color: #d0d7e9;
      padding: 0.24rem;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center; }
  .card__side {
    display: block;
    max-width: 100%;
    height: 100%;
    transition: all 0.2s; }
    .card__side-front {
      text-transform: uppercase;
      display: grid;
