.open .header__lists {
  display: grid;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  grid-template-columns: max-content;
  grid-auto-rows: 15vh;
  justify-content: center;
  background-color: #110503;
  align-content: center;
  animation: nav 1s ease-in-out; }

.open .header__icon-open {
  display: none; }

.open .header__icon-close {
  display: block;
  justify-self: end; }

.open .header__nav {
  margin-top: 4rem; }

.open .header__list {
  text-align: center;
  width: 16rem;
  padding: 1.6rem 0;
  box-shadow: 1px 1px 2.4px 0 #e51c1e; }

.grid {
  display: grid;
  grid-template-rows: [h-h-start] min-content [h-h-end m-start] auto [m-end f-start] auto [row-end];
  grid-template-columns: [fullstart] minmax(6rem, 1fr) [after-bleed-start] repeat(8, [cont-start] minmax(min-content, 14rem) [cont-end]) [before-bleed-end] minmax(6rem, 1fr) [end]; }

.header {
  grid-column: after-bleed-start/before-bleed-end;
  grid-row: h-h-start/h-h-end;
  padding: 2.4rem 0;
  z-index: 10;
  align-self: self-start; }
  .header__nav {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(30%, 1fr));
    align-items: center;
    z-index: 100; }
  .header__button {
    display: none;
    background: none; }
    @media only screen and (max-width: 740px) {
      .header__button {
        display: block;
        justify-self: end;
        position: relative;
        z-index: 100; } }
  .header__icon {
    width: 4rem;
    height: 4rem;
    fill: #d0d7e9; }
    @media only screen and (max-width: 740px) {
      .header__icon-close {
        border: 1px solid #e51c1e;
        border-radius: 50%;
        display: none; } }
  .header__lists {
    list-style: none;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(9rem, 1fr));
    justify-items: end;
    align-items: center; }
    @media only screen and (max-width: 740px) {
      .header__lists {
        display: grid;
        width: 100%;
        position: absolute;
        top: -100%;
        left: 0;
        height: 100vh;
        grid-template-columns: max-content;
        grid-auto-rows: 15vh;
        justify-content: center;
        background-color: #110503;
        align-content: center;
        transition: all 1s; } }
  .header__list {
    transition: all 0.4s; }
    .header__list:hover {
      transform: translateY(-4px); }
  .header__link:link, .header__link:visited {
    text-decoration: none;
    font-size: 1.6rem;
    color: #d0d7e9;
    text-transform: uppercase;
    transition: all 0.4s; }
  .header__link:hover, .header__link:active {
    color: #e51c1e; }
  .header__logo {
    height: 4.8rem;
    width: auto;
    z-index: 100; }

.footer {
